.checkbox-wrapper-42 input[type="checkbox"] {
  visibility: hidden;
  display: none;
}

.checkbox-wrapper-42 label {
  display: inline-block;
}

.checkbox-wrapper-42 .cbx {
  vertical-align: middle;
  cursor: pointer;
  border: 1px solid #c8ccd4;
  border-radius: 3px;
  width: 17px;
  height: 17px;
  transition: background .1s;
  position: relative;
  top: 1px;
}

.checkbox-wrapper-42 .cbx:after {
  content: "";
  opacity: 0;
  border-bottom: 2px solid #fff;
  border-right: 2px solid #fff;
  width: 5px;
  height: 11px;
  transition: all .3s .15s;
  position: absolute;
  top: 1px;
  left: 5px;
  transform: rotate(45deg)scale(0);
}

.checkbox-wrapper-42 .lbl {
  vertical-align: middle;
  cursor: pointer;
  margin-left: 5px;
}

.checkbox-wrapper-42 input[type="checkbox"]:checked ~ .cbx {
  background: #6871f1;
  border-color: #0000;
  animation: .6s jelly-42;
}

.checkbox-wrapper-42 input[type="checkbox"]:checked ~ .cbx:after {
  opacity: 1;
  transform: rotate(45deg)scale(1);
}

.checkbox-wrapper-42 .cntr {
  text-align: center;
  width: 100%;
  position: absolute;
  top: 50%;
  left: 0;
}

@keyframes jelly-42 {
  from {
    transform: scale(1);
  }

  30% {
    transform: scale(1.25, .75);
  }

  40% {
    transform: scale(.75, 1.25);
  }

  50% {
    transform: scale(1.15, .85);
  }

  65% {
    transform: scale(.95, 1.05);
  }

  75% {
    transform: scale(1.05, .95);
  }

  to {
    transform: scale(1);
  }
}

