.optionButtons:before, .optionButtons:after {
  border-radius: 80px;
}

.optionButtons:before {
  content: "";
  z-index: -2;
  background-image: linear-gradient(92.83deg, #227fe9 0, #0f41c9 100%);
  width: 100%;
  height: 100%;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

.optionButtons:after {
  background-color: initial;
  content: "";
  z-index: -1;
  background-image: linear-gradient(#1b27d8 0, #216b6b 100%);
  transition: all .1s ease-out;
  display: block;
  position: absolute;
  inset: 4px;
  overflow: hidden;
}

.optionButtons:hover:not(:disabled):before {
  background: linear-gradient(92.83deg, #227fe9 0%, #0f41c9 100%);
}

.optionButtons:hover:not(:disabled):after {
  opacity: 0;
  transition-timing-function: ease-in;
  inset: 0;
}

.optionButtons:active:not(:disabled) {
  color: #ccc;
}

.optionButtons:active:not(:disabled):before {
  background-image: linear-gradient(0deg, #0003, #0003), linear-gradient(92.83deg, #227fe9 0, #0f41c9 100%);
}

.optionButtons:active:not(:disabled):after {
  background-image: linear-gradient(#541a0f 0, #0c0d0d 100%);
  inset: 4px;
}

.optionButtons:disabled {
  cursor: default;
  opacity: .24;
}

